import NotFound from '../Pages/NotFound/NotFound';
import Home from '../Pages/Home/Home';
import Nouns from '../Pages/Nouns/Nouns';
import Verbs from "../Pages/Verbs/Verbs";
import Test from "../Pages/Test/Test";

const notFoundPath = '*';
const homePath = '/';
const nounsPath = '/nouns';
const verbsPath = '/verbs';
const testPath = '/test';

export const routes = {
    noteFound: {
        path: notFoundPath,
        component: NotFound
    },
    home: {
        path: homePath,
        component: Home
    },
    nouns: {
        path: nounsPath,
        component: Nouns
    },
    verbs: {
        path: verbsPath,
        component: Verbs
    },
    test: {
        path: testPath,
        component: Test
    }
};